import React, { Component } from 'react'
import { Table, Divider, Button, message } from 'antd'
import { goodstypelist, deletegoodstype } from '../../api/goods'
import datefilter from '../../tool/datefilter'
import { statusfilter } from '../../tool/statusfilter'
import { Link } from 'react-router-dom'

const { Column } = Table
class role extends Component {
	constructor(props) {
		super(props)
		this.state = {
			data: []
		}
	}
	getlist() {
		goodstypelist()
			.then((res) => {
				if (res.status === 1) {
					this.setState({
						data: res.data
					})
				}
			})
			.catch((err) => {
				console.log('err: ', err)
			})
	}
	componentDidMount() {
		this.getlist()
	}
	deletegoodstype(id) {
		deletegoodstype({
			id
		})
			.then((res) => {
				console.log('res: ', res)
				this.getlist()
				message.success(res.message)
			})
			.catch((err) => {
				console.log('err: ', err)
			})
	}
	render() {
		return (
			<div>
				<Link to={'/addgoodstype'}>
					<Button type="primary"></Button>
				</Link>
				<Table dataSource={this.state.data}>
					<Column title="" dataIndex="title" key="title" />
					<Column
						title=""
						dataIndex="status"
						key="status"
						render={(text, record) => <div type="primary">{statusfilter(text)}</div>}
					/>

					<Column title="" dataIndex="description" key="description" />
					<Column
						title=""
						key="add_time"
						dataIndex="add_time"
						render={(text, record) => <div type="primary">{datefilter(text)}</div>}
					/>
					<Column
						title=""
						key="_id"
						dataIndex="_id"
						render={(text, record) => (
							<span>
								<Link to={'/goodstypeattribute?id=' + text}>
									<Button type="primary"></Button>
								</Link>
								<Divider type="vertical" />
								<Link to={'/addgoods?id=' + text}>
									<Button type="primary"></Button>
								</Link>
								<Divider type="vertical" />
								<Button type="danger" onClick={this.deletegoodstype.bind(this, text)}>
									
								</Button>
							</span>
						)}
					/>
				</Table>
			</div>
		)
	}
}

export default role
